.dark-mode {
  background-color: #171c28;
  color: white;
  transition: "0.1s";
}

.stack-image{
    width: auto;
    height: 3rem;
}

.stack-element{
  padding-right: 1.6%;
  text-align: center;
  font-size: x-small;
  min-width: none;
}

.stack-container{
  display: flex;
  float: left;
  overflow: hidden;
  width: 100%;
}

@media only screen and (min-width: 1100px) {
  .main{
    padding-left: 25%;
    padding-right: 25%;
  }  
}