.headroom--pinned, .headroom--scrolled{
  padding-left: 25%;
  padding-right: 25%;
}

.greeting-parallax{
  min-height: 600px;
  width: 90%;
  height: auto;
}

.greeting-main {
  display: flex;
  /* margin: 0; */
  /* margin-top: 0.2rem; */
}

.greeting-main > * {
  flex: 1;
  margin-bottom: 30px;
}
.button-greeting-div {
  display: flex;
  margin-top: 20px;
}

.greeting-text {
  text-align: center;
  font-size: 70px;
  line-height: 1.1;
}

.greeting-text-p {
  font-family: Helvetica, sans-serif;
  font-size: 25px;
  line-height: 45px;
}

.greeting-image-div > img {
  width: 90%;
  height: auto;
  border-radius: 20px;
}


/* Media Query */
@media (max-width: 1380px) {
  .greeting-text {
    font-size: 50px;
  }
  .greeting-text-p {
    font-size: 20px;
  }
}
@media (max-width: 768px) {
  .button-greeting-div {
    justify-content: space-around;
  }
  .greeting-text {
    font-size: 30px;
    text-align: center;
  }
  .greeting-text-p {
    font-size: 16px;
    line-height: normal;
    text-align: center;
  }
  .greeting-main {
    display: block;
  }
  .greeting-image-div > img {
    width: 80%;
    height: auto;
    border-radius: 20px;
    margin: auto auto;
    display: grid;
  }
}

@media (max-width: 900px){
  .headroom--pinned, .headroom--scrolled{
    padding-left: 0;
    padding-right: 0;
  }
}
